import * as React from "react";
import Layout from "../../components/layout";
import { graphql } from "gatsby";

const FilmIllustrations = ({ data }) => (
  <Layout>
    <>
      <h1 className="text-orange-400 drop-shadow text-center">Film</h1>

      <div className="max-w-full grid grid-cols-3 lg:grid-cols-5">
        {data.allFile.edges.map((edge) => {
          return (
            <div className="" key={edge.node.base}>
              <img
                // loading="lazy"
                src={edge.node.publicURL}
                alt={edge.node.base}
              />
            </div>
          );
        })}
      </div>
    </>
  </Layout>
);

export default FilmIllustrations;

export const indexQuery = graphql`
  query Illustrations {
    allFile(filter: { sourceInstanceName: { eq: "filmIllustrations" } }) {
      edges {
        node {
          base
          publicURL
        }
      }
    }
  }
`;
